.App {
  text-align: center;
  background: rgb(82,184,246);
  background: linear-gradient(180deg, rgba(82,184,246,1) 0%, rgba(82,184,246,1) 65%, rgba(255,255,255,1) 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: #fff;
  padding: 0 4rem;
}
.question {
  margin-bottom: 50px;
  color: #000;
}
.question .MuiChip-root {
  font-size: 1.8rem;
  padding: 30px;    
  margin-bottom: 10px;
  font-weight: bold;
}
.MuiButton-outlinedPrimary {
  color: #2942cc !important;
}
.answer button {
  font-size: 2rem;
  margin: 20px;
  min-width: 200px;
  text-transform: none;
  font-weight: bold;
}
.listCards{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: space-around;
}
.hints{
  color:#000;
  font-size: 1.2rem;
}
.quitButton{
  position: absolute !important;
  left: 20px;
  top: 20px;
}
.explanation{
  color: #000;
}